.catalog-list {
    padding: 60px 0 30px 0;
    &__items {
        display: grid; 
        justify-content: center;
        grid-template: 1fr / repeat(3, minmax(auto, 220px));
        column-gap: 70px;
        row-gap: 60px;
    }
    &__item {
        position: relative;
        padding: 35px 20px 20px;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        gap: 14px;
        border-radius: 8px;
        box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.25);
        overflow: hidden;
    }
    &__img {
        max-width: 151px;
        flex: 1 1 auto;
        align-self: center;
    }
    &__price {
        font-weight: 700;
    }
}